.about{
    padding-bottom: 48px;
}

.about-top{
    display: flex;
    justify-content:space-between;
    align-items: center;
}

.about-picture{
    height: 420px;
}

.info-name{
    color: blue;
    font-size: 32px;
}

.dark-info-name{
    color: lightskyblue;
}

@media only screen and (max-width: 720px){
    .about-top{
        flex-direction: column;
        justify-content:center;
        align-items: center;
    }

    .about-picture{
        height: 280px;
    }

    .about-info{
        font-size:20px;
    }

    .info-name{
        font-size:26px;
    }
}